import Step from "../../base/step/step.vue"
import Scroll from '../../base/scroll/scroll.vue'
import Upload from '../../base/upload/upload.vue'
export default {
  name: "textImg",
  components: {
    Step,
    Scroll,
    Upload
  },
  data() {
    return {
      isOk: true,
      goodsImg: [],
      goodCommon: '',
      goodsObj: {},
      shopName: ''
    }
  },
  created() {
    //
    let that = this;
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      'buyerTaskRecordId': that.$route.query.buyerTaskRecordId
    }).then((data) => {
      console.log(data)
      if (data.data.code === '200') {
        this.goodsObj = data.data.data
        this.shopName = this.goodsObj.shopName
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      } else {
        this.$vux.alert.show({
          title: '',
          content: data.data.message,
        })
      }
    }).catch(function (err) {
      console.log(err)
    })
  },
  watch: {
    goodsImg: {
      handler(val) {
        if (val.length > 3) {
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        }
      },
      deep: true
    }
  },
  methods: {
    doNext() {
      let that = this;
      if (this.goodsImg.length < 3) {
        this.$vux.alert.show({
          title: '',
          content: '3',
        })
        return false
      }
      if (this.goodCommon === '') {
        this.$vux.alert.show({
          title: '',
          content: '!',
        })
        return false
      }
      this.$axios.post('/api/orderOperate/backOrderSubmit', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
        favorText: this.goodCommon,
        favorPicId: this.goodsImg
      }).then((data) => {
        console.log(data)
        if (data.data.code === '200') {
          this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate1' } })
        } else {
          this.$vux.alert.show({
            title: '',
            content: data.data.message,
          })
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    }
  }
}



